.container {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
}

.link {
    width: 33%;
    text-decoration: none;
    color: #000000;
}

@media (max-width: 1024px) {
    .link {
        width: 50%;
    }
}

@media (max-width: 470px) {
    .link {
        width: 100%;
    }
}